import React, { Component } from "react";
import InfoWidget from '../FooterWidget/InfoWidget';
import Widgets from "../FooterWidget/Widgets";

class FooterTop extends Component {

  state = {language: 'es'};

  render() {
    let FooterData = this.props.FooterData;

    return (
      <>
        <div className="footer_top">
          <div className="container">
              <div className="row">                                    

                  <InfoWidget   />
                  {
                      FooterData.footerWidget.map((item) => {
                          return(
                            <Widgets
                            titleWidget={item.nameWid}
                              WidData={item.wids}
                              />
                          )
                      })
                  }

                  <div className="col-lg-2 col-sm">
                    <div className="f_widget about-widget  pl_30">
                      <img src={require("../../img/home/Dreamcode_2023_Certification_Badge.png").default} alt="Certification Badge DreamCode Software" width="165"/>
                    </div>
                  </div>
              </div>
          </div>
        </div>
      </>              
    );
  }
}
export default FooterTop;