import React, {Component} from 'react';
import { translate } from "react-translate";


class PracticesDevOps extends Component{
    render(){

        let {t} = this.props;

        return(
            <section className="devops_practices_area pb-5 sec_pad">
                <div className="container">
                    <div className="devops_practices_item ">
                        <div className="row align-items-center flex-row-reverse">
                            <div className="col-lg-6 devops_practices_bg">
                                <div className="devops_practices_content  fadeInDown">
                                    <h3>{t(this.props.DevOpsTitle)}</h3>
                                    <p>{t(this.props.DevOpsDescrip)}</p>
                                    
                                </div>                   
                            </div>
                            <div className="col-lg-6 devops_practices_bg">
                                <div className="devops_practices_content  fadeInDown">
                                    <h3>{t(this.props.DevOpsTitle2)}</h3>
                                    <p>{t(this.props.DevOpsDescrip2)}</p>                           
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
        )
    }
}

export default translate('PracticesDevOps')(PracticesDevOps);
