import React, {Component} from 'react';
import { translate } from "react-translate";
import { Link} from 'react-router-dom';
import SeoTitle from '../Title/SeoTitle';
import "./ourProcess.css"

class OurProcess extends Component {
    render(){
        let { t } = this.props;
        
        return(
        
            <section className="our_process_area sec_pad" id="process">
                <div className='py-3 px-2'>
                    <SeoTitle 
                        Tclassp="p_color text-center mb-4" 
                        Title={t(this.props.process_title)} 
                        TitleP={t(this.props.process_subtitle)} 
                        Tclass2="t_color f_size_40 f_600"/>
                </div>
                <div className="container ">
                    <div className="row ">
                        <div className="col-lg-6 d-flex justify-content-center " >
                            <div className="our_process_feedback position-relative col_scrum"> 
                                <a className="w-100" href="/scrum">
                                    <div className="clients_feedback_item">
                                            <div className="feedback_body">
                                            <h3 className="f_name text-left">Agile Scrum</h3>
                                                <p className="text-left f_size_15 w_color f_700">{t(this.props.process_scrum_d)}</p>
                                                <Link to="/scrum" exact className="learn_btn mt-4 "><strong>{t(this.props.read_more)}</strong></Link>
                                            </div>
                                    </div>
                                </a>                            
                            </div>
                        </div>
                        <div className="col-lg-6 d-flex justify-content-center " >
                            <div className="our_process_feedback position-relative col_devops">
                                <a className="w-100" href="/devops">
                                    <div className="clients_feedback_item">
                                        <div className="feedback_body">
                                            <h3 className="f_name text-right">DevOps</h3>
                                            <p className="text-right f_size_15 w_color f_700">{t(this.props.process_devops_d)}</p>
                                            <Link to="/devops" exact className="learn_btn mt-4 "><strong>{t(this.props.read_more)}</strong></Link>
                                        </div>               
                                    </div>                                  
                                </a>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
        
        )
    }
}

export default translate('process_home')(OurProcess);

