import React from 'react';
import ReactGA from 'react-ga';
import "./careersSingle.css";
import { useParams , Redirect} from "react-router-dom";
import BannerFormCareers from './BannerFormCareers';
import SliderJobs from './SliderJobs';
import JobsProfileRol from './JobsProfileRol';
import JobsProfileValue from './JobsProfileValue';
import MetaDecorator from '../MetaDatos/MetaDatosPost';


import jobslistES from "../../ofertas/jobsES.json"
import jobslistEN from "../../ofertas/jobsEN.json"




const JobsProfile = (props) => {

  let jobsLang = null
  let { topicId } = useParams();
  let jobExists = false;
  const fetcheJob = {}

    //Configuración de Google Analytics
        const gaTrackingId = "UA-194698672-1"; 
        ReactGA.initialize (gaTrackingId); 
        ReactGA.pageview ("/careers/" + topicId );

        jobslistES.forEach((jobs, i) => {
            if (topicId === jobs.url) {
                fetcheJob.id = jobs.id ? jobs.id : 'No existe'
                fetcheJob.nameJob = jobs.nameJob ? jobs.nameJob : 'No existe'
                fetcheJob.descrip = jobs.descrip ? jobs.descrip : 'No existe'
                fetcheJob.expert = jobs.expert ? jobs.expert : 'No existe'
                fetcheJob.rol = jobs.rol ? jobs.rol : 'No existe'
                fetcheJob.url = jobs.url ? jobs.url : 'No existe'
                jobExists = true
            }
        })

        if (jobExists === false){
            return <Redirect to='/careers' />
        }

        if (props.language === 'es') {
            jobsLang = jobslistES
          } else {
            jobsLang = jobslistEN
          }

    return (
        <>   
            { jobsLang.map((post, i) => {
                if (post.url === topicId) {
                    return ( 
                            <>   
                                <MetaDecorator 
                                    meta_title={fetcheJob.nameJob}
                                    meta_description={fetcheJob.descrip}
                                    url={"careers/" + fetcheJob.url}
                                />
                                <div className={`${"bg_" + post.color} jobsSingleBanner sec_pad `}>
                                    <div className='container position-relative'>
                                        <h1 className="pt-4 w_color text-center pb-2"><strong>DreamCoder:<br></br></strong> {post.nameJob}</h1>
                                        <p className='text-center w_color px-4 pb-3'>{post.descrip}</p>
                                    </div>
                                </div>
                                <JobsProfileValue 
                                    SubtitleHowYouCan= "SubtitleHowYouCan"
                                    titleHowYouCan = "titleHowYouCan"
                                    JobsExpert1 = "JobsExpert1" 
                                    JobsExpert2 = "JobsExpert2"
                                    JobsExpert3 = "JobsExpert3"
                                    JobsExpert4 = "JobsExpert4"
                                    expert = {post.expert}
                                    urlVideo = "urlVideo"
                                />
                                <JobsProfileRol
                                    color = {`${post.color}`}
                                    rol = {post.rol}
                                    titleRol = "titleRol"
                                />
                                <SliderJobs 
                                    titleSlider = "titleSlider"
                                    sliderJobSingle1="sliderJobSingle1"
                                    sliderJobSingle2="sliderJobSingle2"
                                    sliderJobSingle3="sliderJobSingle3"
                                    sliderJobSingle4="sliderJobSingle4"
                                    sliderJobSingle5="sliderJobSingle5"
                                    sliderJobSingle6="sliderJobSingle6"
                                    sliderJobSingle7="sliderJobSingle7"
                                    sliderJobSingle8="sliderJobSingle8" 
                                />
                                <BannerFormCareers 
                                    BtnBanner = "BtnBanner"
                                    titleFormCareers = "titleFormCareers"
                                    idBanner="CareersForm"
                                    btn_color={`${post.color}`}
                                    classBanner2={`${post.color}`} />        
                            </>
                    );
                }else {
                     <Redirect to="/careers" />
                }
            }
            
            )}
            
        </>
        
      );

      
}
export default JobsProfile;