import React, {Component} from 'react';
import { translate } from "react-translate";
import emailjs from 'emailjs-com';
import ReCAPTCHA from "react-google-recaptcha";


class FormCareers extends Component {

    render(){
      
  const btn = document.querySelector("form button[type=submit]");
  if (btn && btn.addEventListener) { 
  	btn.addEventListener("click",function() { 
      window.lintrk('click',{ conversion_id: 8841764 }); 
    });
  } 

        let { t, btn_color } = this.props;
       
        function sendEmail(e) {
            e.preventDefault();
        
            emailjs.sendForm('service_gmail', 'template_careers', e.target, 'user_DK3x36R2nsn4W5NwGBIou')
              .then((result) => {
                console.log(result.text + "ENVIADO");
            }, (error) => {
                console.log(error.text + "ERROR");
              });
              e.target.reset()

              const modal = document.querySelector(".modal")
              const closeBtn = document.querySelector(".close")
              modal.style.display = "block";
              closeBtn.addEventListener("click", () => {
                modal.style.display = "none";
              })
            
          }
        
          function onChange(value) {
            console.log("Captcha value:", value);
          }


        return(
            <section className="mt-5 form_landing" >
                <div className="container">
                    <div className="row">
                        <div className="contact_form_landing col-lg-12 f_p f_600 ">
                            <form onSubmit={sendEmail} className="contact_form_box needs-validation was-validated" method="post" id="contactForm">
                                <div className="row text-left">
                                    <div className="col-lg-6">
                                        <div className="form-group">
                                            <input className="form-control " color="white" type="text" id="name" name="name" placeholder={t("name")} onChange={this.handleChange} required/>
                                        </div>
                                    </div>
                                    <div className="col-lg-6">
                                        <div className="form-group">
                                            <input className="form-control" type="email" name="email" id="inputEmail4" placeholder={t("email")} onChange={this.handleChange} required/>
                                        </div>
                                    </div>
                                    <div className="col-lg-6">
                                        <div className="form-group">
                                            <input className="form-control" type="text" name="celular" id="Celular"  placeholder={t("celular")}  onChange={this.handleChange} required/>
                                        </div>
                                    </div>
                                    <div className="col-lg-6">
                                        <div className="form-group">
                                            <input className="form-control" type="text" name="linkedin" id="Linkedin"  placeholder="Linkedin" onChange={this.handleChange} required/>
                                        </div>
                                    </div>
                                    <div className="col-lg-6">
                                        <div className="form-group position-relative">
                                            <label className="w_color" for="validationServer02">{t("fileName")}</label>
                                            <input type="file" name="file" id="file" accept="application/pdf, .doc, .docx, .odf" className="inputfile" required/>
                                                <label className={`btn-file btn_form ${btn_color} text-left f_p w-100 f_600`} >
                                                    <span>{t("file")}</span>
                                                </label>
                                                <div className="valid-feedback">{t("fileValid")}</div>
                                        </div>
                                    </div>
                                    <div className="col-lg-6">
                                        <div className="form-group">
                                            <label className="w_color" for="validationServer02">{t("fluidez")}</label>
                                            <select name="ingles" placeholder={t("fluidez")} className={` w_color custom-select mr-sm-2 f_p f_600`}>
                                                <option name="Si" value="Si">{t("yes")}</option>
                                                <option name="No" value="No">No</option>
                                            </select>                                        
                                        </div>
                                    </div>
                                    <div className="col-lg-12">
                                        <div className="form-group">
                                            <input className="form-control" type="text" id="rol" name="rol" placeholder={t("rol")} onChange={this.handleChange} required/>
                                        </div>
                                    </div>
                                    <div className="col-lg-12">
                                        <div className="form-check text-left mb-3">
                                            <input className="form-check-input position-static" type="checkbox" id="blankCheckbox" value="option1" aria-label="..." required/>                        
                                            <label className="pl-2 w_color form-check-label" for="gridCheck">{t("txt_privacyPolicy")}<a href={require ("../../Doc/Tratamiento_de_Datos_Dreamcode.pdf").default} className='w_color' download="Tratamiento_de_Datos_Dreamcode.pdf">{t(this.props.privacyPolicy)}</a></label>
                                        </div>
                                    </div>
                                </div>
                                <div className="mb-3">
                                    <ReCAPTCHA
                                        sitekey="6Le64SIfAAAAAEoQdq4n9_iMt8dGeqWeYgJzs5qe"
                                        onChange={onChange}
                                        size="invisible"
                                    />
                                </div>    
                                <button type="submit" className={`mb-1 ${btn_color} btn_form f_p f_600 `} javascript="window.lintrk('click',{ conversion_id: 8841764 });" >{t(this.props.btn)}</button> 

                            </form>
                            {/* {emailStatus ? emailStatus : null} */}
                          
                            <div className="modal" >
                                <div className=" modal-dialog modal-dialog-centered">
                                    <div className="modal-content">
                                    <div className="modal-header">
                                    
                                        <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                                        <span className="close" aria-hidden="true">&times;</span>
                                        </button>
                                    </div>
                                    <div className="modal-body">
                                        <h3 className=' f_p text-center'>{t(this.props.textModal)}</h3>
                                    </div>
                                    </div>
                                </div>
                            </div>

                    </div>
                    </div>
                    
                </div>
            </section>
        )
    }
}

 
export default translate('careers_form')(FormCareers);