import React, {Component} from 'react';
import { translate } from "react-translate";
import MenuItems from "./MenuItems";
import "./header.css";

const MenuEn = require('../../assets/translations/en.json');
const MenuEs = require('../../assets/translations/es.json');
let lang = null;

class MenuPrincipal extends Component {
    
    render() {
        let { t, onChangeLanguage, language, valueProps} = this.props;

        if (language === 'en') {
            lang = MenuEn
          } else {
            lang = MenuEs
          } 



            return (
                    <header className={`header_area`} id="header">
                        <nav className="navbar navbar-expand-lg menu_one menu_four">
                            <div className="container">
                                <a className="navbar-brand f_size_0 sticky_logo" href="/">
                                    <img className="logoBlue" src={require("../../img/logo-blue.png").default} alt="logo DreamCode Software" title="Logo DreamCode Software" width="164"/>                             
                                </a>
                                <button className="navbar-toggler collapsed" type="button" data-toggle="collapse" data-target="#OnePageManuContent" aria-controls="OnePageManuContent" aria-expanded="false" aria-label="Toggle navigation">
                                    <span className="menu_toggle">
                                        <span className="hamburger">
                                            <span></span>
                                            <span></span>
                                            <span></span>
                                        </span>
                                        <span className="hamburger-cross">
                                            <span></span>
                                            <span></span>
                                        </span>
                                    </span>
                                </button>

                                <div className="collapse navbar-collapse menu justify-content-end" id="OnePageManuContent">
                                    <ul className="navbar-nav ml-auto mr-3 w_menu">
                                        {
                                            lang.menu_item.map((item, i) => {
                                                return (
                                                    <MenuItems 
                                                        key={i}
                                                        linkMenu={item.linkMenu}
                                                        textItem={item.textItem} 
                                                    />
                                                )
                                            })
                                                
                                        }    
                                    </ul>
                                    <ul className="navbar-nav d-flex flex-direction-row">
                                        <li className="dropdown submenu nav-item">
                                            <a className="btn_get btn_hover btn_get_radious" activeclass="active" href="/blog">{t('menu_blog')}</a>
                                        </li>
                                        <li className="dropdown submenu nav-item">
                                            <a className="btn_get btn_hover btn_get_radious" activeclass="active" href="/careers">{t('menu_jobs')}</a>
                                        </li>
                                    </ul>
                                    <ul className="pl-4 navbar-nav w_menu lang">                                                                 
                                        <li className="dropdown submenu nav-item">
                                            <div  >
                                                <select className="select-lang" defalutValue="" value={valueProps} onChange={(valueProps)=>{onChangeLanguage(valueProps)}} >  
                                                    <option value="en" >EN</option>
                                                    <option value="es" >ES</option>
                                                </select>              
                                            </div>
                                        </li>   
                                    </ul> 
                                </div>
                            </div>
                        </nav>
                    </header>
            );
    }
}

export default translate('menu')(MenuPrincipal);