import React, {  useState } from "react";
import "./SliderDreamcoders.css"
import "./dreamcoders.css"
import { translate, useTranslate } from "react-translate";
import { FreeMode, Navigation, Grid, Autoplay, Thumbs } from "swiper";
import { Swiper, SwiperSlide } from 'swiper/react/swiper-react';

const langEn = require('../../assets/translations/en.json');
const langEs = require('../../assets/translations/es.json');
let lang = null;

const SliderDreamcoders = (props) => {

  const [thumbsSwiper, setThumbsSwiper] = useState(null);

  let t = props.t

  let lg = useTranslate('lang');
  if (lg('language') === 'es') {
    lang = langEs
  } else {
    lang = langEn
  }

  return (
    <div className="sliderDreamcoders" id="SliderCopy">
      <div className="container">
        <div className="row">
          <div className="col-lg-6">  
            <Swiper
              loop={true}
              spaceBetween={10}
              navigation={false}
              autoplay={{
                delay: 6000,
                disableOnInteraction: false,
              }}
              thumbs={{ swiper: thumbsSwiper }}
              modules={[Autoplay, FreeMode, Navigation, Thumbs]}
              className="mySwiper2"
            >

            { lang.dreamcoders.TestimoDreamcoders.map((item, i) => {
                  return (
                    <SwiperSlide key={i}>
                    <div >
                        <div className="item w-100 text-left">
                            <h2 className="a_title f_size_20 f_600">{t("sliderDreamcodersTitle")}</h2>                            
                            <h2 className="content f_size_20">{item.Testimonial}</h2>                            
                            <div className="testimonial_author text-left position-relative">
                                <h6 className="w_color f_600 f_size_20 l_height10 name">{item.name}</h6>                                                                    
                                <div className="m_color f_600 f_size_18 position designation">{item.position}</div>
                            </div>
                        </div>
                    </div>
                    </SwiperSlide>
                 )
              })
            }
            </Swiper>
          </div>
          <div className="col-lg-6"> 
            <Swiper
              onSwiper={setThumbsSwiper}
              slidesPerView={2}
              grid={{
                rows: 2,
              }}
              spaceBetween={10}
              
              freeMode={true}
              rewind={true}
              modules={[FreeMode, Grid, Navigation, Thumbs]}
              className="mySwiper"
            >
              <SwiperSlide>
                <img src={require("../../img/dreamcoders/Dreamcoder-Ered.png").default}  alt="Dreamcoders Ered" width="160" height="160"/>
              </SwiperSlide>
              <SwiperSlide>
              <img src={require("../../img/dreamcoders/Dreamcoder-Juan.png").default} alt="Dreamcoders Juan" width="110" height="110"/>
              </SwiperSlide>
              <SwiperSlide>
              <img src={require("../../img/dreamcoders/Dreamcoder-Viviana.png").default} alt="Dreamcoders Viviana" width="110" height="110"/>
              </SwiperSlide>
              <SwiperSlide>
              <img src={require("../../img/dreamcoders/Dreamcoder-Eliana.png").default} alt="Dreamcoders Eliana" width="160" height="160"/>
              </SwiperSlide>
            </Swiper>
            <img className="svg_intro_bottom " src={require ('../../img/dreamcoders/fondo-dreamcoders.png').default}  alt="Fondo dreamcoders"/>
          </div>
        </div> 
      </div>
    </div>
  );
}

export default translate('dreamcoders')(SliderDreamcoders);