import { useState } from 'react';
//import { sanitize } from '../../utils/miscellaneous';
//import Loading from '../../utils/loading';
import { translate } from "react-translate";
import "./newsletter.css";


const NewsletterForm = ( { status, onValidated , t }) => {

  const [ error, setError ] = useState(null);
  const [ email, setEmail ] = useState(null);


  
  const newsletterEn = require('../../assets/translations/en.json');
  //const newsletterEs = require('../../assets/translations/es.json');

  /**
   * Manejar envío de formulario.
   *
   * @return {{value}|*|boolean|null}
   */
    const handleFormSubmit = () => {

      setError(null);

      if ( ! email ) {
        setError( 'Por favor, introduce una dirección de correo electrónico válida' );
        console.log('error');
        return null;
        
      }

      const isFormValidated = onValidated({ EMAIL: email });

      // En caso de éxito devuelve verdadero
      return email && email.indexOf("@") > -1 && isFormValidated;
    }

  /**
   * Manejar evento de clave de entrada.
   * @param event
   */

  const handleInputKeyEvent = ( event ) => {
    setError(null);
    // El número 13 es la tecla "Enter" en el teclado
    if (event.keyCode === 13) {
      // Cancele la acción predeterminada, si es necesario
      event.preventDefault();
      // Active el elemento del botón con un clic
      handleFormSubmit();
    }
  }


  return (
      <div className="modal-content ">
        <div className={`subscribe_form_info module text-center`}>
            <ul className="list-unstyled figure_animation">
                <li><img src={require ("../../img/home/fondo-home0.png").default} alt="" width="40" /></li>
                <li><img src={require ("../../img/home/fondo-home1.png").default} alt="" width="40" /></li>
                <li><img src={require ("../../img/home/fondo-home2.png").default} alt="" width="60" /></li>
                <li><img src={require ("../../img/home/fondo-home.png").default} alt="" width="60" /></li>
                <li><img src={require ("../../img/home/fondo-home0.png").default} alt="" width="40" /></li>
                <li><img src={require ("../../img/home/fondo-home1.png").default} alt="" width="40" /></li>
                <li><img src={require ("../../img/home/fondo-home2.png").default} alt="" width="60" /></li>
                <li><img src={require ("../../img/home/fondo-home.png").default} alt="" width="60" /></li>
                
            </ul>   
            <h3 className="f_size_30 pl-3 m-0 p_color">{t("newsletterH2")}</h3>
            <h2 className="f_size_50 f_600 mb-1 p_color">DreamBlog</h2>
            <h4 className='f_size_18 f_p text-center p_color'>{t("newsletterH4")}</h4>
            <p className='f_size_15 p_color'>{t("newsletterP")}</p>
            <form action="#" className="subscribe-form">
                <input
                onChange={(event) => setEmail(event?.target?.value ?? '')}
                type="email"
                placeholder="YOUR E-MAIL"
                className="f_700 text-center border-dark w-100 px-3 py-2 rounded-lg border bg-white "
                onKeyUp={(event) => handleInputKeyEvent(event)}
                />
                <button type="submit" className="f_700 w-100 cursor-pointer border-dark border px-3 b_color py-2 rounded-lg my-2 text-center" onClick={handleFormSubmit}>{t("newsletterBtn")}</button>
            </form>
            
        </div>
        <div className="col-lg-12 text-right">
          { 'sending' === status ?  <div>{newsletterEn.newsletter.sending}</div>
            : null }
          {'error' === status || error ? (
            <div className=" pt-3" >{newsletterEn.newsletter.error}</div>
          ) : null }
          {'success' === status && 'error' !== status && !error && (
            <div className=" font-bold pt-2" >{newsletterEn.newsletter.success}</div>
          )}
        </div>
      </div> 
  );
}

export default translate('newsletter')(NewsletterForm);
