import React, {Component} from 'react';
import Fade from 'react-reveal/Fade';
import "./homeServices.css"

import { Autoplay, Navigation } from "swiper";
import { Swiper, SwiperSlide } from 'swiper/react/swiper-react';

class HomeServices extends Component {

    render(){
        let LangData = this.props.LangData;

        return(
            <>
            <section className="homeServices bg-light" id='home-services'>
                <div className="container">
                    <div className="row">
                        <div className="col-lg-5 d-flex align-items-center">
                            <div className="home-services-title">
                                <Fade bottom cascade>
                                    <div className=" fadeInUp" data--delay="0.3s">
                                        <h2 className="t_color f_600 f_size_40">{LangData.service_home.serv_title}</h2>
                                        <p className="p_color f_600 f_size_18">{LangData.service_home.serv_subtitle}</p>
                                        <a className="t_color mt-3 f_500 f_size_20 f_p" href="/services"><strong>Read More</strong></a>                                    
                                      </div>
                                </Fade>
                            </div>
                        </div>
                        <div className="col-lg-6 offset-lg-1 ">
                            
                            <div className="stratup_testimonial_info d-flex align-items-center">
                              
                              <Swiper
                                slidesPerView={1}
                                spaceBetween={30}
                                centeredSlides={true}
                                autoplay={{
                                  delay: 25000,
                                  disableOnInteraction: false,
                                }}
                                navigation={true}
                                modules={[Autoplay, Navigation]}
                                className="swiper-services-home"
                              >
                                  {
                                    LangData.service_home.servitems.map((item, i) => { 
                                      return(
                                        <SwiperSlide className="ServItem " key={i}>
                                          
                                              <div className='w-50 ml-4'>
                                                <h5 className='text-center f_600 mb-3 mx-4'>{item.servTitle}</h5>
                                              </div>                                                  
                                              <div className='mx-4'>
                                                <p className='mx-4 text-center f_size_20'>{item.servDescrip}</p>
                                              </div>
                                        
                                        </SwiperSlide>
                                      )
                                    })
                                  }
                                </Swiper>
                              
                            </div>
                        </div>
                    </div>
                </div>
            </section>
            </>
        )
    }
}
export default HomeServices;