import React, { Component } from "react";
import DreamcodersItem from './DreamcodersItem';
import SeoTitle from '../Title/SeoTitle'; 
import { translate } from "react-translate";
const DreamcodersEn = require('../../assets/translations/en.json');
const DreamcodersEs = require('../../assets/translations/es.json');
let lang = null;

class Dreamcoders extends Component {
    
    render() {

        let { t, language } = this.props;

        if (language === 'en') {
            lang = DreamcodersEn
          } else {
            lang = DreamcodersEs
          }

        return(
            <section className="experts_team_area bg-light" id="jobsTeam">
                <div className=' pb-5'>
                    <SeoTitle  Title={t(this.props.DreamcodersTitle)} Tclass="text-center pt-5" Tclass2="t_color f_size_40 f_600" />
                </div>
                <div className="container">
                    <div className="row">
                            {
                                lang.dreamcoders.dreamcodersItems.map((item, i) => {
                                    return ( 
                                        <DreamcodersItem 
                                            ImgDreamcoders={item.ImgDreamcoders} 
                                            dreamcoderName={item.dreamcoderName}
                                            position={item.position}
                                            verb={item.verb}
                                        />
                                    )
                                })
                            }
                    </div>
                </div>
            </section>
        
        );
    }
}

export default translate('dreamcoders')(Dreamcoders);
