import React, { Component } from 'react';
import MailchimpSubscribe from 'react-mailchimp-subscribe';
import NewsletterForm from './NewsletterForm';
import $ from 'jquery';



class BodyNewsletter extends Component {

    render() {

        
        let { nameId, className, classNameTwo } = this.props
        const url = "https://dreamcodesoft.us14.list-manage.com/subscribe/post?u=978d25ae0ee8fb9d3e00da841&amp;id=c41f989908";

    /* Tiempo en que sale el Pop-up */
    $(document).ready(
        function() {
          $('.modal').delay(150000).queue(function() {
            $('#exampleModalCenter').modal('show')
          });
        }
      );
            
    
        return (
            <div className={`${className} newsletter `} id={`${nameId}`} tabIndex="-2" role="dialog" aria-labelledby="exampleModalCenterTitle" aria-hidden="true">
                
                <div className={`${classNameTwo}`}>
                    
                    <MailchimpSubscribe 
                        url={ url }
                        render={ (props) => {
                            const { subscribe, status } = props || {};

                            return (
                                <NewsletterForm
                                status={ status }
                                onValidated={ formData => subscribe( formData ) } 
                                />
                            );
                        }}
                    />
                </div>    
            </div> 
        );
    }
}

export default BodyNewsletter;
