import React, { Component } from "react";
import { Helmet } from "react-helmet";

class MetaDatosPost extends Component {
    render() {
  
      let {meta_title, meta_description, url} = this.props;
  
      return (   
        <Helmet>
            <meta property="og:title" content= {meta_title} />
            <meta property="og:url" content={`https://dreamcodesoft.com/${url}`} />
            <meta property="og:description" content= {meta_description} />
            <title>{meta_title} </title>
            <meta name="description" content={meta_description} />
            <link rel="canonical" href={`https://dreamcodesoft.com/${url}`}/>
        </Helmet>
       );
    };
  };




  export default MetaDatosPost;