import React, {Component} from 'react';
import { translate } from "react-translate";



class InfoWidget extends Component {
    render(){
        let { t } = this.props;

        return(
            <div className="col-lg-4 col-sm-6">
                <div className="f_widget company_widget" >
                    <a href="/" className="f-logo "><img src={require("../../img/logo-blue.png").default} alt="Logo DreamCode Software" width="165"/></a>
                    <div className="widget-wrap mt_20 f_p f_600">
                        <p className="f_size_18 mb-2"><span>{t('info_widgetEmail')}:</span> <a href="mailto:info@dreamcodesoft.com"  className="f_500">info@dreamcodesoft.com</a></p>
                        <p className="f_size_18 mb-2"><span>{t('info_widgetPhone')}:</span> <a href="tel:573155790099"  className="f_500">+57 315 220 62 11</a></p>
                        <p className="f_size_18 mb-2"><span>Colombia: </span><a href="https://goo.gl/maps/Ync42KoKat2ckRW86"  className="f_500"> Carrera 100 #16-321, <br></br>{t('AddressItem')}, Cali, Colombia.</a></p>
                        <p className="f_size_18 mb-2"><span>USA: </span><a href="https://goo.gl/maps/DGBgT8RAojBZghKK7"  className="f_500"> 50 California Street, 15th Floor, <br></br>San Francisco, CA 94111-4701, United States. </a></p>
                    </div>
                </div>
            </div>
        )
    }
}

export default translate('info_widget')(InfoWidget);