import React, { Component } from "react";

import JobsItems from '../Careers/JobsItems';
import SeoTitle from '../../components/Title/SeoTitle'; 
import { translate } from "react-translate";

const CareersEs = require('../../ofertas/jobsES.json');
const CareersEn = require('../../ofertas/jobsEN.json');

let lang = null;

class JobsTab extends Component {

  render() {

    let { t, language } = this.props;

    if (language === 'es') {
        lang = CareersEs
      } else {
        lang = CareersEn
      }

    return (
        <section>
            <div className="mt-5">
            <SeoTitle  Title={t(this.props.titleCareers)} Tclass="text-center pt-5" Tclass2="t_color f_size_40 f_600" />
            </div>
            <div className="pb-4 pt-4">
                <div className=" container">
                    <div className="tab-content row jobs_content pb-5 justify-content-center">
                {
                    lang.map((item, i) => {
                    return (
                        <>
                        <JobsItems  
                            id= {item.id}
                            active = {item.active}
                            nameJob = {item.nameJob}
                            color={item.color}
                            url = {item.url}
                            apply = "apply"
                            remote = "remote"
                        />  
                                        
                        </>
                    )
                    })               
                }             
                    </div>
                </div>
            </div>
        </section>          
    );
  }
}
export default translate('careers')(JobsTab);
