import React , {Component} from 'react';
import { translate } from "react-translate";
import RedesSociales from '../Flotante';
import FooterBottom from './FooterBottom';
import FooterTop from './FooterTop';
import BodyNewsletter from '../Newsletter/BodyNewsletter'


import "./footer.css";

class Footer extends Component {

    state = {language: 'en'};
    
    render(){
        let FooterData = this.props.FooterData;
           
    
        return(
            <>
                <footer className="footer_area footer_area_four f_bg">
                    <FooterTop FooterData={FooterData}/>
                    <BodyNewsletter 
                        className = "modal fade"
                        classNameTwo = "modal-dialog modal-xl px-2"
                        nameId = "exampleModalCenter"
                        language={this.props.language}/> 
                    <FooterBottom                         
                        Intranet="Intranet"
                        privacyPolicy="privacyPolicy"
                    />                 
                </footer>
                <RedesSociales mensajeWhatsapp="mensajeWhatsapp"/>
            </>
        )
    }
}

export default translate('footer')(Footer);