import React, {Component} from 'react';
import Reveal from 'react-reveal/Reveal/';
import { translate } from "react-translate";
import "./banners.css"


class ProcessBanner extends Component{
    render(){


            let { t , img_process} = this.props;

        return(
            <section className="banner_area min-vh-100 text-left bg_grey sec_pad mt-5">
                <div className="container">
                
                        <div className="row">
                            <div className="col-lg-7  seo_banner_content ">
                                <Reveal effect="fadeInUp" duration={500}>
                                    <h1 className="mb-5 f_size_60 t_color f_700">{t(this.props.title_banner)}</h1>
                                
                                </Reveal>
                                <Reveal effect="fadeInUp" duration={1000}>
                                    <p className="fadeInUp mb-5 pr_0" data--delay="0.5s">
                                        {t(this.props.descri_banner)}</p>
                                </Reveal>
                                
                            </div>
                            <div className="col-lg-5" >
                            <Reveal effect="fadeInRight" duration={1200}>
                                    <div className="banner_img" >
                                        <img src={require ("../../img/process/" + img_process).default} alt="Process Scrum DevOps"/>               
                                    </div>
                            </Reveal>
                            </div>
                        </div>
                        <div id="section10" className="demo text-center mt-5">
                                    <div><span></span></div>
                        </div>
                        <p className="f_p p_color text-center f_600">Scroll</p>

                </div>
            </section>
        )
    }    
}


export default translate('sec_banner')(ProcessBanner);
