import React, {Component} from 'react';

class DreamcodersItem extends Component {
    render(){
        var {ImgDreamcoders, position ,dreamcoderName, verb}= this.props;
        return(
            <div className="col-lg-3 col-6">
                <div className="ex_team_item">
                    <img src={require ("../../img/dreamcoders/" + ImgDreamcoders).default} alt="DreamCoders Verbs Culture"/>
                    <div className="team_content text-left pl-3 ">
                        <h3 className="f_p f_size_25 f_600 w_color">{dreamcoderName}</h3>
                        <h4 className='w_color f_p f_size_18 f_500'>{position}</h4>
                    </div>
                    <div className="hover_content">
                        <div className="n_hover_content"> 
                            <h3 className="mb-4 verb_team f_p f_size_30 f_600 w_color">{verb}</h3>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}
export default DreamcodersItem;