import React, {Component} from 'react';


class Widgets extends Component {

    render(){

        let { titleWidget} = this.props;
        let footerWidget = this.props.WidData;

        return(

            <div className="col-lg-3 col">
                <div className="f_widget about-widget  pl_30">
                    <h3 className="f-title f_600 t_color f_size_20 mb_20">{titleWidget}</h3>
                    <ul className="list-unstyled f_list">
                        {
                            footerWidget.map((item, i) => {
                                return(
                                    <li key={i}>
                                        <a activeclass="active" spy="true" offset={0} duration={500} href={item.url}>{item.text}</a>
                                    </li>                                
                                )
                            })
                        }
                    </ul>
                </div>
            </div>

        )
    }
}

export default Widgets;