import React from 'react';
import { BrowserRouter as Switch,Route,useRouteMatch, Redirect} from "react-router-dom";
import BannerVideo from '../components/Banner/BannerVideo';
import BannerSecondJob from '../components/Careers/BannerSecondJob';
import JobsTab from '../components/Careers/JobsTab';
import MetaDecorator from '../components/MetaDatos/MetaDecorator';
import ReactGA from 'react-ga';
import "../components/Careers/jobsnew.css"
import Dreamcoders from "../components/Careers/Dreamcoders";

// Page 
import JobsProfile from '../components/Careers/JobsProfile';
//import JobsProfile from '../components/Careers/JobsProfileCopy';
import Benefits from '../Pages/Benefits';

const Careers = (props) => {

    let { path } = useRouteMatch();

    // Configuración de Google Analytics
    const gaTrackingId = "UA-194698672-1"; 
    ReactGA.initialize (gaTrackingId); 
    ReactGA.pageview ("/careers");

    return (
        <>
            <MetaDecorator 
                meta_title="metaTitle_Jobs"
                meta_description="metaDescription_Jobs"
                url="careers"
            />
            <Switch>
                <Route exact path={path}>
                    <BannerVideo
                        urlVideo="urlVideo"
                    />
                    <BannerSecondJob 
                        BannerTitle = "BannerTitle"
                        BannerSubTitle = "BannerSubTitle"
                        BannerSubTitle2 = "BannerSubTitle2"
                        BannerTxt = "BannerTxt"
                    />
                    <JobsTab titleCareers="titleCareers" language = {props.language}/>
                    <Dreamcoders 
                        DreamcodersTitle="DreamcodersTitle"
                        language = {props.language}
                    />
                    <Benefits />

                </Route> 
            
                <Route path={`${path}/:topicId`} > 
                    <JobsProfile language={props.language} />
                </Route>
            </Switch>
            
        </>
    )
}

export default Careers;