import React, {Component} from 'react';
import { Swiper, SwiperSlide } from 'swiper/react/swiper-react';
import { Autoplay } from "swiper";
//import Slider from 'react-slick';
import Markdown from "react-markdown"
import postlist from "../../posts.json"
//import postlistEs from "../../postsES.json"


class HomeSliderBlog extends Component {


   render(){

    postlist.sort((a, b) => new Date(a.date).getTime() < new Date(b.date).getTime())

        const excerptList = postlist.map(post => {
            return post.content.split(" ").slice(0, 18).join(" ") + "..."            
        })


        return(
            <section className="blog_home_area bg-light">
                <Swiper 
                    autoplay={{ delay: 250000, 
                    disableOnInteraction: false,}}
                    modules={[Autoplay]}
                    class="swiper-blog-home" >
                        
                        { 
                            postlist.slice(0, 10).map((post, i) => {
                                return (
                                    <SwiperSlide className="blog_item text-center left" key={i}>
                                        <div className="row m-0">
                                            <div className="col-lg-7 p-0">
                                                <img className="img-fluid w-100" src={require(`../../img/posts-Img/${post.imglink}`).default} alt={post.title}/>
                                            </div>
                                            <div className="col-lg-5 p-0 contentBlog">
                                                <div className="text-left contentBlogItem">
                                                    <div className="feedback f_p">
                                                        <a href={`/${post.url}`}>
                                                            <h2 className="text-uppercase f_p f_size_25 f_600 w_color mb_20">{post.title}</h2>
                                                        </a>
                                                        <div className="entry_post_info  mb-2 text-capitalize">
                                                            <a href={`/${post.url}`} className="w_color ">{post.date}</a>
                                                            <a href={`/${post.url}`} className="w_color ">{post.read}</a>                                                        
                                                        </div>
                                                        <Markdown children={excerptList[i]} skipHtml={false} className="post-text f_size_18"/>
                                                        <a href={`/${post.url}`} className="f_size_18 f_600 w_color learn_btn_two">Read More</a>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>    
                                    </SwiperSlide>
                                )
                            })
                        }
                </Swiper>
            </section>
        )
   }
}
export default HomeSliderBlog;