import React, {Component} from 'react';

import { translate} from "react-translate";
import "./banners.css"

class ServBanner extends Component{
    render(){


            let { t , idBanner, classBanner, paddingClass, titleClass} = this.props;


        return(
            <section className={`banner_area ${classBanner} sec_pad mt-5`} id={idBanner}>
                <div className="container">
                
                        <div className="row">
                            <div className="col-md-12 seo_banner_content ">
                                <div>
                                    <h1 className={`mb-5 f_size_60 f_700 ${titleClass} `} >{t('title_banner_Services', {name: <span>{t('tailored')}</span> })}</h1>
                                </div>
                                <div className='pt-3'>
                                    <p className={`fadeInUp mb-5 ${paddingClass} `} data--delay="0.5s">
                                        {t('descri_banner_Services')}</p>
                                </div>
                                
                            </div>
                        </div>
                        <div id="section10" className="demo text-center mt-5">
                                    <div><span></span></div>
                        </div>
                        <p className="f_p p_color text-center f_600">Scroll</p>

                </div>
            </section>
        )
    }    
}


export default translate('sec_banner')(ServBanner);
