import React, { Component } from "react";
import { translate } from "react-translate";


class JobsItems extends Component {
  render() {

    let { nameJob, url , id, color, t, active } = this.props;


    return (
              <div className={`col-lg-4 p-1 ${active}`} id={id} >
                <div className={`media ${"bg_" + color} flex-wrap`} >
                    <div className="media-body  w_color">
                      <h3 className="f_size_25 f_700" >{nameJob}</h3>   
                      <h4 className="f_size_20 pt-2 pb-2 f_600 ">{t(this.props.remote)}<i className="pl-2 f_size_15 f_600 ti-rss-alt"></i></h4>
                      <button type="button" className="f_p f_700 btn btn-light">
                            <a href={"/careers/"+ url} className={`${color}`} >{t(this.props.apply)}</a>
                      </button>
                    </div>
                </div>  
              </div>            
    );
  }
}
export default translate('careers')(JobsItems);
