import React, {Component} from 'react';
import { translate } from "react-translate";




const MenuEn = require('../../assets/translations/en.json');
const MenuEs = require('../../assets/translations/es.json');
let lang = null;

class ServiceItems extends Component {

    constructor(){
        super();
        this.state= {
            language: 'en'
        }
    }


    render(){

        let { t , styleColor,language ,style2, style3, image} = this.props;
        
        if (language === 'en') {
            lang = MenuEn
          } else {
            lang = MenuEs
          } 

    return(
        <div className={`servItems  ${styleColor}`}>
            <div className="container">   
                    <div className={`row ${style2} services_content`}>
                        <div className="col-lg-8 d-flex align-items-center">
                            <div className="content-item">
                                <h2 className="f_600 f_size_40">{t(this.props.TitleServ)}</h2>
                                <p className="f_p f_500 w_color">{t(this.props.DescrServ)}</p>
                                <ul className={`f_p f_500 f_size_20 w_color ${style3}`}>
                                    {
                                        lang.ServicePage.Servitems.map((value) => {
                                            return <li>{value}</li>
                                        })    
                                    }
                                </ul>
                            </div>
                        </div>
                        <div className="col-lg-4">
                            <div className="imgServ d-flex justify-content-center">
                                <img src={require ("../../img/services/" + image).default} alt={t(this.props.TitleServ)} width="50"/>
                            </div>
                        </div>
                    </div>
            </div>
        </div>


        )
    }
}

export default translate('ServicePage')(ServiceItems);