
import React, { Component } from 'react';
import { BrowserRouter, Route ,Switch, Redirect } from "react-router-dom";
import { TranslatorProvider } from "react-translate";
import { Helmet } from "react-helmet";

/* ---- Pages ----*/
import Home from "./Pages/Home";
import NotFound from "./Pages/404";
import Devops from "./Pages/Devops";
import Scrum from "./Pages/Scrum";
import Contact from "./Pages/Contact";
import Blog from "./Pages/Blog"
import Services from "./Pages/Services"
import Category from "./Pages/Category"
import PostSingle from "./Pages/PostSingle"
import Careers from "./Pages/Careers";
import Dreamcoders from './Pages/Dreamcoders';
import Nosotros from "./Pages/Nosotros";
import GreatPlace from "./Pages/GreatPlace"

/* ---- Menu y Footer ----*/
import Menu from './components/Header/Menu';
import Footer from "./components/Footer/Footer";

import "./assets/home.css";
import "./assets/main.css";
import "swiper/swiper-bundle.css";
import "bootstrap/dist/js/bootstrap.min.js";
import "bootstrap/dist/css/bootstrap.min.css";
import "./assets/themify-icon/themify-icons.css";
import "./assets/animate.css";
import "./assets/simple-line-icon/simple-line-icons.css";
import "./assets/font-awesome/css/all.css";
import "./assets/elagent/style.css";
import "./assets/responsive.css";

class App extends Component{
 	
    constructor(props) {
        super(props);

        this.state = {
            language: 'en'
        }; 
        //bind crear una copia de la funcion
        this.languageChange = this.languageChange.bind(this);  
    }

    
    //Cambio el valor del state.langugage segun el select deel menu
    languageChange(event) {
        this.setState({
            language: event.target.value
            
        }); 
        
    }

    


    getLayout(lang){


    const translationsLang = require('./assets/translations/'+lang+'.json');

	   return  (    
        <TranslatorProvider translations={translationsLang}>	
            <Helmet><link rel="alternate" hreflang={translationsLang.locale} href="https://www.dreamcodesoft.com/" /></Helmet>
                <BrowserRouter >
                    <Menu  valueProps={this.state.value} 
                        onChangeLanguage={this.languageChange} 
                        language={this.state.language}
                        
                        
                    />
                        <Switch>
                            <Route exact path={"/"} component={() => <Home language={this.state.language} LangData={translationsLang}/>} />                                                    
                            <Route path="/contact" component={() => <Contact language={this.state.language} />} /> 
                            <Route path="/devops" component={Devops} />
                            <Route path="/scrum" component={Scrum} />
                            <Route path="/blog/" component={Blog} />
                            <Route path="/about" component={Nosotros} />
                            <Route path="/great-place-to-work" component={GreatPlace} />
                            <Route path={"/dreamcoders"} component={() => <Dreamcoders language={this.state.language} />} /> 
                            <Route path={"/careers"} component={() => <Careers language={this.state.language} />} /> 
                            <Route path={"/category"} component={() => <Category language={this.state.language} />} />
                            <Route path={"/services"} component={() => <Services language={this.state.language} />} />
                            <Route path="/404" component={NotFound} />  
                            <Route exact path="/:url" component={props => <PostSingle {...props} language={this.state.language}/>} />         
                            <Redirect to="/blog"  />   
                        </Switch>                 
                    <Footer FooterData={translationsLang} />
                </BrowserRouter>

        </TranslatorProvider>
       )

   }

   render(){
    
	   return(
           this.getLayout(this.state.language)
           
        );
    };    
}; 


export default App;