import React, {Component} from 'react';
import { translate } from 'react-translate';


class BannerVideo extends Component{


    render(){

        let { t } = this.props;

        return(
            <section className='jobsVideo-section '>
                <div className=" jobsVideo-background-video-container">
                        <iframe className="jobsVideo-background-video-embed" 
                                frameBorder="0" 
                                allowFullScreen
                                allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"  
                                title="Sueña para transformar el futuro." 
                                width="640" 
                                height="360" 
                                src={t(this.props.urlVideo)} >
                        </iframe>            
                </div>
            </section>
        )
    }    
}


export default translate('JobsProfile')(BannerVideo);
